body {
    margin: 0;
    padding: 0;
    user-select: none;
}
  
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nbScreen__select {
  display: none;
  position: absolute;
  bottom: 2vh;
  right: 1vw;
  border: 5px solid black;
  border-radius: 50%;
  padding: 1em 0.5em;
  transform: translate(-50%, -50%);
}

.screens__container__1 {
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(1, 1fr);
  width: 100vw;
  height: 100vh;
}

.screens__container__2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, 1fr);
  width: 100vw;
  height: 100vh;
}

.screens__container__4 {
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  width: 100vw;
  height: 100vh;
}

.frame {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: auto;
}

.frame__rotate__left {
  transform: rotate(90deg);
}

.frame__rotate__right {
  transform: rotate(-90deg);
}

.frame__reverse {
  transform: rotate(180deg);
}

.frame__container {
  position: relative;
  width: 100%;
  height: 100%;
}

.preloader__container {
  position: absolute;
}

.canvas__confettis {
  position: absolute;
  pointer-events: none;
  width: 100%;
  height: 100%;
}